main {
    blockquote {
        display: block;
        border-width: 4px 0;
        border-style: solid;
        border-color: #eee;
        padding: 1.5em 1em 0.5em;
        margin: 1.5em 1em;
        position: relative;
        &:before {
            content: '\201C';
            position: absolute;
            top: 0em;
            left: 50%;
            transform: translate(-50%, -50%);
            background: #fff;
            width: 3rem;
            height: 2rem;
            font: 6em/1.08em $font, sans-serif;
            color: #666;
            text-align: center; }
        &:after {
            content: "\2013 \2003" attr(cite);
            display: block;
            text-align: right;
            font-size: 0.875em;
            color: #e74c3c; } }
    table {
        font-size: 0.9rem;
        margin-bottom: 4em;
        @include desktop {
            border-spacing: $main-padding/2 0.3em;
            border-collapse: separate;
            margin-left: -$main-padding;
            width: calc(100% + #{$main-padding * 2}); }
        thead th {
            text-align: left;
            border-bottom: 4px solid #eee; }
        td {
            padding: 0 0em; } }
    img {
        display: block;
        margin: 0 auto;
        max-height: 20vh;
        max-width: calc(100% - 4em);
        outline-offset: 4px;
        outline: 1px solid #aaa;
        transition: 250ms;
        width: auto;
        @include desktop {
            float: right;
            margin: 0 (-$main-padding - 8em) 2em 2em;
            max-height: initial;
            max-width: calc(65%);
            transform: rotate(2deg); }
        &:hover {
            filter: sepia(0.3); }
        &.svg {
            float: none;
            max-width: 100%;
            outline: none;
            transform: initial;
            margin: 2em 0; } }
    audio {
        width: 100%;
        background: lighten($primary, 20);
        border-radius: 0.2em; }
    figure {
        figcaption {
            text-align: right; } } }

#captcha-modal {
    display: none;
    position: fixed;
    bottom: 25vh;
    background: $white;
    padding: 3em 7em 2em;
    border: 1px solid rgba(0,0,0,0.1);
    z-index: 2;
    box-shadow: 0px 2px 9px rgba(0,0,0,0.2); }

.cross {
    color: $text;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 1.3em;
    padding: 0.5em 0.6em;
    transition: 200ms;
    opacity: 1;
    &:hover {
        opacity: 0.8; } }
