header {
    box-sizing: border-box;
    margin-bottom: 2em;
    max-width: $linewidth + 40px;
    padding: 0;
    width: 100%;
    z-index: 2;
    @include desktop {
        padding-left: 2em; }
    h1 {
        display: inline-block;
        box-sizing: border-box;
        margin: 0;
        a {
            font-variation-settings: "wght" 600; }
        a:hover {
            font-variation-settings: "wght" 700; }
        a, a:hover {
            transition: 250ms;
            display: block;
            padding: 0.4em 0.5em;
            color: $primary;
            text-decoration: none;
            font-size: 1.6rem;
            @include desktop {
                font-size: 2.7rem; }
            @include iphone {
                font-size: 1.2rem; } } }
    h1, nav .menu, .menu-icon {
        background: $white;
        @include desktop {
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px; } } }

.backdrop {
    overflow: hidden;
    z-index: -2;
    position: absolute;
    top: 0;
    min-height: 1em;
    max-height: 13em;
    width: 100%;
    img {
        width: 100%;
        @include desktop {
            transform: translateY(-7em); }
        @include iphone {
            width: auto;
            height: 500px; } }
    &.full {
        min-height: 500px;
        max-height: 40vh; } }
